
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import tools from '../../lib/tools.js';
import moduleTitle from '../../components/ModuleTitle';

export default {
name:'myOrderList',
components:{moduleTitle,Swipeout, SwipeoutItem, SwipeoutButton,Confirm},
directives: {TransferDom},
data(){
return {
id:'',
showDelConfirm:false,
delId:'',
showToast:false,
toastMsg:'',
orderAry:[]
}

},
mounted(){
this.loading();
},
methods:{
loading(){
var vm = this;
let id = this.id = localStorage.getItem('md') || tools.getRequestParam('md') || tools.getCookie('user_id');
let pageSize=1000,pageNum=1;
let data = {"showCount": pageSize,"currentPage": pageNum,"userId":id,"orderCode": '',"relationName": "","relationSupplier": "","startDate": "","endDate": "","type": "","payStatus": ''};
this.$http.axios({method:'post',url:'/orderInfo/getOrderInfoArray',data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.items){
vm.orderAry = data.items;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
document.querySelector('html').style.height = '100%';
document.body.style.height = '100%';
},
getDetail(id){
window.location.href="/m/orderDetail.html?id="+id;
},
codeStr(str){
var code = '';
var len = str.length;
return code = str.substring(0,6)+'******'+str.substring(len-6,len);
},
deleteOrder(id){
this.showDelConfirm = true;
this.delId = id;
},
onJGConfirm(){
let vm = this;
this.$http.axios({method:'get',url:'/orderInfo/deleteOrderInfo?id='+this.delId}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.showToast = true;
vm.toastMsg = '删除成功';
vm.orderAry = vm.orderAry.filter(item => item.id!=this.delId);
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
});
},
}
}



